<template>
  <el-form ref="form" label-width="70px" class="login-container" :model="form" :rules="rules" :inline="true">
    <h3 class="login-title">系统登录</h3>
    <el-form-item label="用户名" prop="username">
      <el-input type="text" v-model.trim="form.username" placeholder="请输入用户名"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="password">
      <el-input type="password" v-model.trim="form.password" placeholder="请输入密码" @keyup.enter.native="submit"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submit">登 陆</el-button>
    </el-form-item>
    <el-form-item>
      <el-button @click="resetInput">重 置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import Cookie from 'js-cookie'
import { getMenu } from '../api'

export default {
  data() {
    return {
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [{
          required: true,
          trigger: 'blur',
          message: '请输入用户名',
        }],
        password: [{
          required: true,
          trigger: 'blur',
          message: '请输入密码',
        }]
      }
    }
  },
  methods: {
    // 登陆
    submit() {
      this.$refs.form.validate((valid) => {
        if(valid) {
          getMenu(this.form).then(({ data }) => {
            if(data.code === 20000) {
              // yarn add js-cookie@3.0.1
              // 将 token 信息存入 cookie 用于不同页面间的通信
              Cookie.set('token', data.data.token)

              // 将获取的菜单数据，存入 store 中
              this.$store.commit('setMenu', data.data.menu)

              this.$store.commit('addMenu', this.$router)

              this.$router.push('/home')
              this.$message({
                message: this.form.username + '，欢迎回来',
                type: 'success'
              })
            } else {
              this.$message.error(data.data.message)
            }
          })
        }
      })
    },
    resetInput() {
      this.$refs.form.resetFields()
    }
  },
}
</script>

<style lang="less" scoped>
  .login-container {
    width: 350px;
    margin: 180px auto;
    border: 1px solid #eaeaea;
    padding: 35px 35px 15px 35px;
    background-color: #fff;
    border-radius: 15px;
    box-shadow: 0 0 25px #cac6c6;
    box-sizing: border-box;
    .login-title {
      text-align: center;
      margin-bottom: 40px;
      color: #505458;
    }
    .el-input {
      width: 198px;
    }
    .el-button {
      margin: 10px 20px 0 55%;
    }
  }
</style>